// import axios from "axios";

let lsSinyalHuobi1InchErc = JSON.parse(localStorage.getItem("mw_sinyal_huobi1inch_erc"));

const state = {
  sinyal: lsSinyalHuobi1InchErc != null ? lsSinyalHuobi1InchErc.sinyal : [],
  lastFetch: lsSinyalHuobi1InchErc != null ? lsSinyalHuobi1InchErc.lastFetch : null,
};

const getters = {
  listSinyalHuobi1InchErc(state) {
    return state.sinyal;
  },
  lastFetchSinyalHuobi1InchErc(state) {
    return state.lastFetch != null ? new Date(state.lastFetch).toLocaleString("id-ID") : "";
  },
};

const actions = {
  registerSinyalHuobi1InchErc(context) {
    let sinyal = [];
    console.log("sinyal", sinyal);
    context.rootState.coin.coins_erc.forEach(item => {
      let askPrice = context.rootGetters.getBookCoinPriceAsk(item.nama_coin);
      let bidPrice = context.rootGetters.getBookCoinPriceBid(item.nama_coin);
      sinyal.push({
        symbol: item.nama_coin,
        pair: askPrice.pair,
        modal_kiri: context.rootState.setting.settings.modal_kiri_huobi1inch_erc || 400,
        modal_kanan: context.rootState.setting.settings.modal_kanan_huobi1inch_erc || 400,
        coin_address: item.coin_address,
        binance_ask_price: askPrice.price,
        binance_bid_price: bidPrice.price,
        inch_usdt: 0,
        selisih_pnl_kiri: 0,
        selisih_pnl_kiri_persen: 0,
        inch_sum: 0,
        inch_price: 0,
        selisih_pnl_kanan: 0,
        selisih_pnl_kanan_persen: 0,
        aktif: item.aktif_1inch != undefined ? item.aktif_1inch : true,
      });
    });
    console.log(sinyal);
    context.commit("registerSinyalHuobi1InchErc", sinyal);
  },
  updateSinyalHuobi1InchErc(context, coin) {
    let sinyal = state.sinyal;
    let index = state.sinyal.findIndex(item => {
      return item.symbol == coin.symbol;
    });
    let symbol = sinyal[index];
    symbol = { ...symbol, ...coin.value };
    sinyal.splice(index, 1, symbol);
    context.commit("registerSinyalHuobi1InchErc", sinyal);
  },
  clearSinyalHuobi1InchErc(context) {
    let sinyal = state.sinyal.map(item => {
      return {
        ...item,
        binance_ask_price: "wait...",
        binance_bid_price: "wait...",
        inch_usdt: "wait...",
        selisih_pnl_kiri: "wait...",
        selisih_pnl_kiri_persen: "wait...",
        inch_sum: "wait...",
        inch_price: "wait...",
        selisih_pnl_kanan: "wait...",
        selisih_pnl_kanan_persen: "wait...",
      };
    });
    // sinyal.filter(item => {{
    // }});
    context.commit("registerSinyalHuobi1InchErc", sinyal);
  },
};

const mutations = {
  registerSinyalHuobi1InchErc(state, payload) {
    localStorage.setItem("mw_sinyal_huobi1inch_erc", JSON.stringify({ sinyal: payload, lastFetch: Date.now() }));
    state.sinyal = payload;
    state.lastFetch = Date.now();
  },
};

export default {
  state,
  actions,
  mutations,
  getters,
};
