import axios from "axios";

let lsHuobi = JSON.parse(localStorage.getItem("mw_huobi"));

const state = {
  bookTicker: lsHuobi != null && lsHuobi.bookTicker != null ? lsHuobi.bookTicker : [],
  lastFetch: lsHuobi != null && lsHuobi.lastFetch != null ? lsHuobi.lastFetch : null,
};

const getters = {
  listHuobiBookTicker(state) {
    return state.bookTicker;
  },
  getHuobiBookCoinPriceAsk: state => coin => {
    let prices = state.bookTicker.filter(
      item => item.symbol == coin + "USDT" || item.symbol == coin + "BUSD" || item.symbol == coin + "BNB" || item.symbol == coin + "BTC" || item.symbol == coin + "ETH",
    );
    let price;
    price = prices.find(item => item.symbol == coin + "USDT");
    if (price != undefined && price.askPrice != 0) {
      return { price: price.askPrice, pair: "USDT" };
    }
    price = prices.find(item => item.symbol == coin + "BUSD");
    if (price != undefined && price.askPrice != 0) {
      return { price: price.askPrice, pair: "BUSD" };
    }
    price = prices.find(item => item.symbol == coin + "BNB");
    if (price != undefined && price.askPrice != 0) {
      let multiply = state.bookTicker.find(item => item.symbol == "BNBUSDT");
      return { price: price.askPrice * multiply.bidPrice, pair: "BNB" };
    }
    price = prices.find(item => item.symbol == coin + "BTC");
    if (price != undefined && price.askPrice != 0) {
      let multiply = state.bookTicker.find(item => item.symbol == "BTCUSDT");
      return { price: price.askPrice * multiply.bidPrice, pair: "BTC" };
    }
    price = prices.find(item => item.symbol == coin + "ETH");
    if (price != undefined && price.askPrice != 0) {
      let multiply = state.bookTicker.find(item => item.symbol == "ETHUSDT");
      return { price: price.askPrice * multiply.bidPrice, pair: "ETH" };
    }
    return { price: 0, pair: "NONE" };
  },
  getHuobiBookCoinPriceBid: state => coin => {
    let prices = state.bookTicker.filter(
      item => item.symbol == coin + "USDT" || item.symbol == coin + "BUSD" || item.symbol == coin + "BNB" || item.symbol == coin + "BTC" || item.symbol == coin + "ETH",
    );
    let price;
    price = prices.find(item => item.symbol == coin + "USDT");
    if (price != undefined && price.bidPrice != 0) {
      return { price: price.bidPrice, pair: "USDT" };
    }
    price = prices.find(item => item.symbol == coin + "BUSD");
    if (price != undefined && price.bidPrice != 0) {
      return { price: price.bidPrice, pair: "BUSD" };
    }
    price = prices.find(item => item.symbol == coin + "BNB");
    if (price != undefined && price.bidPrice != 0) {
      let multiply = state.bookTicker.find(item => item.symbol == "BNBUSDT");
      return { price: price.bidPrice * multiply.bidPrice, pair: "BNB" };
    }
    price = prices.find(item => item.symbol == coin + "BTC");
    if (price != undefined && price.bidPrice != 0) {
      let multiply = state.bookTicker.find(item => item.symbol == "BTCUSDT");
      return { price: price.bidPrice * multiply.bidPrice, pair: "BTC" };
    }
    price = prices.find(item => item.symbol == coin + "ETH");
    if (price != undefined && price.bidPrice != 0) {
      let multiply = state.bookTicker.find(item => item.symbol == "ETHUSDT");
      return { price: price.bidPrice * multiply.bidPrice, pair: "ETH" };
    }
    return { price: 0, pair: "NONE" };
  },
};

const actions = {
  updateHuobiBookTicker(context) {
    return axios
      .get("https://api.huobi.pro/market/tickers")
      .then(response => {
        let bookTicker = response.data.data;
        if (bookTicker[0] != undefined && bookTicker[0].symbol != undefined) {
          let fbookTicker = bookTicker.map(part => {
            return {
              symbol: part.symbol.toUpperCase(),
              askPrice: part.ask,
              bidPrice: part.bid,
            };
          });
          console.log(fbookTicker);
          localStorage.setItem("mw_huobi", JSON.stringify({ bookTicker: fbookTicker, lastFetch: Date.now() }));
          context.commit("updateHuobiBookTicker", fbookTicker);
          return {
            status: "ok",
          };
        } else {
          return {
            status: "error",
            message: "Format respon huobi tidak valid",
          };
        }
      })
      .catch(error => {
        if (error.response) {
          return {
            status: "error",
            message: error.response.status,
          };
        } else if (error.request) {
          return {
            status: "error",
            message: "Tidak bisa menghubungi huobi",
          };
        } else {
          return {
            status: "error",
            message: error.message,
          };
        }
      });
  },
};

const mutations = {
  updateHuobiBookTicker(state, payload) {
    state.bookTicker = payload;
    state.lastFetch = Date.now();
  },
};

export default {
  state,
  actions,
  mutations,
  getters,
};
