// import axios from "axios";

let lsSinyalMexcMatchaBsc = JSON.parse(localStorage.getItem("mw_sinyal_mexc_matcha_bsc"));

const state = {
  sinyal: lsSinyalMexcMatchaBsc != null ? lsSinyalMexcMatchaBsc.sinyal : [],
  lastFetch: lsSinyalMexcMatchaBsc != null ? lsSinyalMexcMatchaBsc.lastFetch : null,
};

const getters = {
  listSinyalMexcMatchaBsc(state) {
    return state.sinyal;
  },
  lastFetchSinyalMexcMatchaBsc(state) {
    return state.lastFetch != null ? new Date(state.lastFetch).toLocaleString("id-ID") : "";
  },
};

const actions = {
  registerSinyalMexcMatchaBsc(context) {
    let sinyal = [];
    console.log("sinyal", sinyal);
    context.rootState.coin.coins_mexc_bsc.forEach(item => {
      let askPrice = context.rootGetters.getMexcBookCoinPriceAsk(item.nama_coin);
      let bidPrice = context.rootGetters.getMexcBookCoinPriceBid(item.nama_coin);
      let mdl_kiri = context.rootState.setting.settings.modal_kiri_mexc_matcha_bsc || 400;
      if (askPrice.volume * askPrice.price < mdl_kiri) {
        mdl_kiri = context.rootState.setting.settings.modal_kiri_mexc_matcha_bsc_min || 50;
      }
      let mdl_kanan = context.rootState.setting.settings.modal_kanan_mexc_matcha_bsc || 400;
      if (askPrice.volume * bidPrice.price < mdl_kanan) {
        mdl_kanan = context.rootState.setting.settings.modal_kanan_mexc_matcha_bsc_min || 11;
      }
      sinyal.push({
        symbol: item.nama_coin,
        pair: askPrice.pair,
        modal_kiri_mexc_matcha_bsc: mdl_kiri,
        modal_kanan_mexc_matcha_bsc: mdl_kanan,
        coin_address: item.coin_address,
        mexc_ask_price: askPrice.price,
        ask_xprice: askPrice.xprice !== undefined ? askPrice.xprice : null,
        mexc_bid_price: bidPrice.price,
        bid_xprice: bidPrice.xprice !== undefined ? bidPrice.xprice : null,
        inch_usdt: 0,
        selisih_pnl_kiri: 0,
        selisih_pnl_kiri_persen: 0,
        inch_sum: 0,
        inch_price: 0,
        selisih_pnl_kanan: 0,
        selisih_pnl_kanan_persen: 0,
        aktif: item.aktif_matcha != undefined ? item.aktif_matcha : true,
        offset_pnl: item.offset_pnl != undefined ? item.offset_pnl : 0,
        status_wd: false,
        status_depo: false,
        fee_wd: 0,
      });
    });
    console.log(sinyal);
    context.commit("registerSinyalMexcMatchaBsc", sinyal);
  },
  updateSinyalMexcMatchaBsc(context, coin) {
    let sinyal = state.sinyal;
    let index = state.sinyal.findIndex(item => {
      return item.symbol == coin.symbol;
    });
    let symbol = sinyal[index];
    symbol = { ...symbol, ...coin.value };
    sinyal.splice(index, 1, symbol);
    context.commit("registerSinyalMexcMatchaBsc", sinyal);
  },
  clearSinyalMexcMatchaBsc(context) {
    let sinyal = state.sinyal.map(item => {
      return {
        ...item,
        mexc_ask_price: "wait...",
        mexc_bid_price: "wait...",
        inch_usdt: "wait...",
        selisih_pnl_kiri: "wait...",
        selisih_pnl_kiri_persen: "wait...",
        inch_sum: "wait...",
        inch_price: "wait...",
        selisih_pnl_kanan: "wait...",
        selisih_pnl_kanan_persen: "wait...",
      };
    });
    // sinyal.filter(item => {{
    // }});
    context.commit("registerSinyalMexcMatchaBsc", sinyal);
  },
};

const mutations = {
  registerSinyalMexcMatchaBsc(state, payload) {
    localStorage.setItem("mw_sinyal_mexc_matcha_bsc", JSON.stringify({ sinyal: payload, lastFetch: Date.now() }));
    state.sinyal = payload;
    state.lastFetch = Date.now();
  },
};

export default {
  state,
  actions,
  mutations,
  getters,
};
