import axios from "axios";

let lsMexc = JSON.parse(localStorage.getItem("mw_mxc"));

const state = {
  bookTicker: lsMexc != null && lsMexc.bookTicker != null ? lsMexc.bookTicker : [],
  lastFetch: lsMexc != null && lsMexc.lastFetch != null ? lsMexc.lastFetch : null,
};

const getters = {
  listMexcBookTicker(state) {
    return state.bookTicker;
  },
  getMexcBookCoinPriceAsk: state => coin => {
    let prices = state.bookTicker.filter(
      item => item.symbol == coin + "_USDT" || item.symbol == coin + "_BUSD" || item.symbol == coin + "_BNB" || item.symbol == coin + "_BTC" || item.symbol == coin + "_ETH",
    );
    let price;
    price = prices.find(item => item.symbol == coin + "_USDT");
    if (price != undefined && price.ask != 0) {
      return { price: price.ask, pair: "USDT", volume: price.volume };
    }
    price = prices.find(item => item.symbol == coin + "_BUSD");
    if (price != undefined && price.ask != 0) {
      return { price: price.ask, pair: "BUSD", volume: price.volume };
    }
    price = prices.find(item => item.symbol == coin + "_BNB");
    if (price != undefined && price.ask != 0) {
      let multiply = state.bookTicker.find(item => item.symbol == "BNBUSDT");
      return { price: price.ask * multiply.bid, pair: "BNB", xprice: price.ask, pairPrice: multiply.ask, volume: price.volume };
    }
    price = prices.find(item => item.symbol == coin + "_BTC");
    if (price != undefined && price.ask != 0) {
      let multiply = state.bookTicker.find(item => item.symbol == "BTCUSDT");
      return { price: price.ask * multiply.bid, pair: "BTC", xprice: price.ask, pairPrice: multiply.ask, volume: price.volume };
    }
    price = prices.find(item => item.symbol == coin + "_ETH");
    if (price != undefined && price.ask != 0) {
      let multiply = state.bookTicker.find(item => item.symbol == "ETHUSDT");
      return { price: price.ask * multiply.bid, pair: "ETH", xprice: price.ask, pairPrice: multiply.ask, volume: price.volume };
    }
    return { price: 0, pair: "NONE" };
  },
  getMexcBookCoinPriceBid: state => coin => {
    let prices = state.bookTicker.filter(
      item => item.symbol == coin + "_USDT" || item.symbol == coin + "_BUSD" || item.symbol == coin + "_BNB" || item.symbol == coin + "_BTC" || item.symbol == coin + "_ETH",
    );
    let price;
    price = prices.find(item => item.symbol == coin + "_USDT");
    if (price != undefined && price.bid != 0) {
      return { price: price.bid, pair: "USDT" };
    }
    price = prices.find(item => item.symbol == coin + "_BUSD");
    if (price != undefined && price.bid != 0) {
      return { price: price.bid, pair: "BUSD" };
    }
    price = prices.find(item => item.symbol == coin + "_BNB");
    if (price != undefined && price.bid != 0) {
      let multiply = state.bookTicker.find(item => item.symbol == "BNBUSDT");
      return { price: price.bid * multiply.bid, pair: "BNB", xprice: price.bid, pairPrice: multiply.bid };
    }
    price = prices.find(item => item.symbol == coin + "_BTC");
    if (price != undefined && price.bid != 0) {
      let multiply = state.bookTicker.find(item => item.symbol == "BTCUSDT");
      return { price: price.bid * multiply.bid, pair: "BTC", xprice: price.bid, pairPrice: multiply.bid };
    }
    price = prices.find(item => item.symbol == coin + "_ETH");
    if (price != undefined && price.bid != 0) {
      let multiply = state.bookTicker.find(item => item.symbol == "ETHUSDT");
      return { price: price.bid * multiply.bid, pair: "ETH", xprice: price.bid, pairPrice: multiply.bid };
    }
    return { price: 0, pair: "NONE" };
  },
};

const actions = {
  updateMexcBookTicker(context) {
    return axios
      .get("https://www.mexc.com/open/api/v2/market/ticker")
      .then(response => {
        let bookTicker = response.data.data;
        console.log("SSS", bookTicker);
        if (bookTicker[0] != undefined && bookTicker[0].symbol != undefined) {
          localStorage.setItem("mw_mxc", JSON.stringify({ bookTicker, lastFetch: Date.now() }));
          context.commit("updateMexcBookTicker", bookTicker);
          return {
            status: "ok",
          };
        } else {
          return {
            status: "error",
            message: "Format respon mexc tidak valid",
          };
        }
      })
      .catch(error => {
        if (error.response) {
          return {
            status: "error",
            message: error.response.status,
          };
        } else if (error.request) {
          return {
            status: "error",
            message: "Tidak bisa menghubungi mexc",
          };
        } else {
          return {
            status: "error",
            message: error.message,
          };
        }
      });
  },
};

const mutations = {
  updateMexcBookTicker(state, payload) {
    state.bookTicker = payload;
    state.lastFetch = Date.now();
  },
};

export default {
  state,
  actions,
  mutations,
  getters,
};
