// import axios from "axios";

let lsSinyalParaswapBnbBsc = JSON.parse(localStorage.getItem("mw_sinyal_paraswap_bnb_bsc"));

const state = {
  sinyal: lsSinyalParaswapBnbBsc != null ? lsSinyalParaswapBnbBsc.sinyal : [],
  lastFetch: lsSinyalParaswapBnbBsc != null ? lsSinyalParaswapBnbBsc.lastFetch : null,
};

const getters = {
  listSinyalParaswapBnbBsc(state) {
    return state.sinyal;
  },
  lastFetchSinyalParaswapBnbBsc(state) {
    return state.lastFetch != null ? new Date(state.lastFetch).toLocaleString("id-ID") : "";
  },
};

const actions = {
  registerSinyalParaswapBnbBsc(context) {
    let sinyal = [];
    console.log("sinyal", sinyal);
    context.rootState.coin.coins.forEach(item => {
      let askPrice = context.rootGetters.getBookCoinPriceAsk(item.nama_coin);
      let bidPrice = context.rootGetters.getBookCoinPriceBid(item.nama_coin);
      sinyal.push({
        symbol: item.nama_coin,
        pair: askPrice.pair,
        modal_kiri: context.rootState.setting.settings.modal_kiri_paraswap_bnb_bsc || 400,
        modal_kanan: context.rootState.setting.settings.modal_kanan_paraswap_bnb_bsc || 400,
        coin_address: item.coin_address,
        binance_ask_price: askPrice.price,
        binance_bid_price: bidPrice.price,
        inch_usdt: 0,
        selisih_pnl_kiri: 0,
        selisih_pnl_kiri_persen: 0,
        inch_sum: 0,
        inch_price: 0,
        selisih_pnl_kanan: 0,
        selisih_pnl_kanan_persen: 0,
        aktif: item.aktif_paraswap != undefined ? item.aktif_paraswap : true,
      });
    });
    console.log(sinyal);
    context.commit("registerSinyalParaswapBnbBsc", sinyal);
  },
  updateSinyalParaswapBnbBsc(context, coin) {
    let sinyal = state.sinyal;
    let index = state.sinyal.findIndex(item => {
      return item.symbol == coin.symbol;
    });
    let symbol = sinyal[index];
    symbol = { ...symbol, ...coin.value };
    sinyal.splice(index, 1, symbol);
    context.commit("registerSinyalParaswapBnbBsc", sinyal);
  },
  clearSinyalParaswapBnbBsc(context) {
    let sinyal = state.sinyal.map(item => {
      return {
        ...item,
        binance_ask_price: "wait...",
        binance_bid_price: "wait...",
        inch_usdt: "wait...",
        selisih_pnl_kiri: "wait...",
        selisih_pnl_kiri_persen: "wait...",
        inch_sum: "wait...",
        inch_price: "wait...",
        selisih_pnl_kanan: "wait...",
        selisih_pnl_kanan_persen: "wait...",
      };
    });
    // sinyal.filter(item => {{
    // }});
    context.commit("registerSinyalParaswapBnbBsc", sinyal);
  },
};

const mutations = {
  registerSinyalParaswapBnbBsc(state, payload) {
    localStorage.setItem("mw_sinyal_paraswap_bnb_bsc", JSON.stringify({ sinyal: payload, lastFetch: Date.now() }));
    state.sinyal = payload;
    state.lastFetch = Date.now();
  },
};

export default {
  state,
  actions,
  mutations,
  getters,
};
